var frames = document.querySelectorAll('.frame-section');
var framesLength = frames.length

gsap.registerPlugin(ScrollTrigger);

var tl = gsap.timeline({
    onComplete: function(){ console.log("TERMINOOOOOOO") },
    scrollTrigger: {
        trigger: ".trigger1",
        start: 'top 50%',
        end: 'bottom 40%',
        scrub: false,
        // onEnter onLeave onEnterBack onLeaveBack
        toggleActions: "play none reverse none",
        snap: {
            snapTo: "trigger1"
            //snap: 1 / (framesLength - 1 )
        },
        markers: {
            fontSize: '2rem'
        }
    }
}, 0);

tl.fromTo(".frame__video", {
        z: 0,
        duration: 1,
        opacity: 1
    }, {
        z: 500,
        duration: 3,
        opacity: 0
},0);

tl.fromTo(".frame__hokkaido", {
        z: -500,
        duration: 1,
        opacity: 0
    }, {
        z: 0,
        duration: 3,
        opacity: 1
},0);

tl.fromTo(".frame__osaka", {
        z: -1000,
        duration: 1,
        opacity: undefined
    }, {
        z: -500,
        duration: 1,
        opacity: undefined
},0);



